import style from './MainPeace.module.scss';

function MainPeace(props) {
  const path = process.env.PUBLIC_URL;
  return (
    <>
      <div className={style.peace}>
        <img src={path + './images/heart-01.svg'} alt="사랑"
          className={style.heart} />
        <img src={path + './images/peace-01.png'} alt="평화와 사랑 그리고 아이스크림"
          className={style.imgPeace} />
        <img src={path + './images/icecream.svg'} alt="아이스크림"
          className={style.icecream01} />

        <h3 className={style.peaceTitle}>
          평화와 사랑 <br /> 그리고 아이스크림
        </h3>
        <p>
          우리는 아이스크림 만들기를 좋아합니다.
          하지만 우리의 사업을 통해 세상을 더 나은 곳으로 만드는 것이 우리의 일에 의미를
          부여합니다. 다시 말해, 우리는 아이스크림을 사용하여 세상을 바꿉니다
        </p>

        <div className={style.btn}>LEARN MORE</div>

        <div className={style.peaceImgBottom}>
          <img src={path + './images/peace-02.png'} alt="평화와 사랑 그리고 아이스크림" className={style.imgPeace02} />
        </div>

      </div>
    </>
  );
}

export default MainPeace;